@import '../../variables';

.Apropos{
    position: relative;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
  
    background-image: url(../../../images/font_etoile.png);
    border-radius: 10px;
    text-align: center;
    padding: 100px 75px;
    gap:40px;
    margin-top: 128px;
    margin-bottom: 128px;

    &::before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: $color-black;
        mix-blend-mode: screen;
        pointer-events: none;
        z-index: 1;
    }

    h3{
        color: #F9F9F9;
        font-size: 45px;
        font-weight: 500;
        margin: 0;
    }

    p{
        color: #BCBDC4;
        font-weight: 300;
    }

    button{
        color: #F9F9F9;
        font-weight: 500;
        padding: 25px 50px;
        border-radius: 40px;
        font-size: 16px;
        font-family: $font-inter;
        border: none;
        background: radial-gradient(65.62% 143.51% at 70.47% 80.29%, #EE4D97 0%, #FFCCE3 100%);
        position: relative;
        overflow: hidden;
        background-size: 200%;
         background-position: center;
        transition: background-position 0.5s ease;
        cursor: pointer;
        

        .bouton-rose-fleche{
            left: 0;
            translate: (-100% 0);
            position: absolute;
            transition: left 0.5s ease;
        }

        &:hover {
            background-position: right;
            

            .bouton-rose-fleche{
                left: 12%;

            }
        }
    

    }

    .imageIa{
        position: absolute;
        top: -89px ;
        left: 50px;
        background: radial-gradient(385.01% 750.38% at 33.08% 51.26%, #A4A4FF 0%, #0016EC 20%) ;
        border-radius: 50%;
    }
}

@media(max-width : 768px){
    .Apropos{
        padding: 50px 35px;
    }

    .imageIa{
        display: none;
    }
}

@media(max-width : 425px){
    .Apropos{
        border-radius: 0;
        padding: 32px 16px;
        margin-bottom: 64px;
        margin-top: 64px;
    }
}